import { axios } from '@/utils/request'

const api = {
    queryVehicleType: '/vehicle',
    getCalculateList: '/insurance',
    getCalculateDetail: '/insurancedetail',
    addInsuranceAccount: '/insuranceaccount',
    editInsuranceAccount: '/insuranceaccount',
    setDefaultInsurance: '/insurancesetdefault'
}

export function queryVehicleType(data) {
    return axios({
        url: api.queryVehicleType,
        method: 'post',
        data:data
    })
}

export function getCalculateList() {
    return axios({
        url: api.getCalculateList,
        method: 'get',
        headers: {
            'Content-Type': 'application/json;charset=UTF-8',
        }
    })
}

export function getCalculateDetail(parameter) {
    return axios({
        url: api.getCalculateDetail,
        method: 'get',
        params: parameter
    })
}

export function addInsuranceAccount(data) {
    return axios({
        url: api.addInsuranceAccount,
        method: 'post',
        data: data
    })
}

export function editInsuranceAccount(data) {
    return axios({
        url: api.addInsuranceAccount,
        method: 'put',
        data: data
    })
}

export function setDefaultInsurance(data) {
    return axios({
        url: api.setDefaultInsurance,
        method: 'put',
        data: data
    })
}



