<template>
  <div class="trackRecord">
    <a-table
        :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
        :pagination="pager"
        :columns="columns"
        :dataSource="trackRecordData"
        @change="handleChange"
        :scroll="{ x: 1300 }"
        :loading="loading"
      >
      <template slot="operation" slot-scope="text, record">
        <a
          href="javascript:;"
          @click="onDelete(record.key)"
        >删除</a>
      </template>
    </a-table>
    <callHistory :customerId="currentCustInfo.id"></callHistory>
  </div>
</template>

<script>
  import ATable from 'ant-design-vue/lib/table';
  import callHistory from "./CallHistory.vue";
  import {
    mapGetters,
    mapActions
  } from "vuex";
  import { getTrackRecordOfDetail, getTrackRecordOfDetailByParams } from '@/api/accounts'
  import {mixin} from '@/utils/mixin'
  export default {
    components: {
      ATable,
      callHistory
    },
    mixins:[mixin],
    data() {
      return {
        loading: false,
        trackRecordData: [],

        //page
        total: 0,
        defaultCurrent: 1,
        currentPage: 0,
        pageSize: 0,

        selectedRowKeys: [], // Check here to configure the default column
        selectedRowId: [],
      };
    },
    created() {
      let _this = this
      this.loading = true
      this.getTrackRecordDataFromServer().then((result) => {
        _this.currentPage = result.current_page;
        _this.pageSize = result.per_page;
        _this.total = result.total;
        _this.loading = false
      }).catch((err) => {
        _this.loading = false
      });
    },
    mounted() {
    },
    watch: {
      currentCustInfo: {
        handler: function(currentCustInfo) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.getTrackRecordDataFromServer();
          }, 1000);
        }
      }
    },
    computed: {
      ...mapGetters(["currentCustInfo"]),
      pager() {
        console.log(this.total)
        console.log(total => `共有 ${total} 条`)
        console.log(this.pageSize)
        console.log(this.defaultCurrent)
        console.log(this.currentPage)
        return {
          total: this.total,
          showTotal: total => `共有 ${total} 条`,
          // showSizeChanger: true,
          showQuickJumper: true,
          // pageSizeOptions: this.pageSizeOptions,
          pageSize: this.pageSize,
          defaultCurrent: this.defaultCurrent,
          current: this.currentPage
          // onShowSizeChange: this.onPagerSizeChange
        };
      },
      columns() {
        //        let {sortedInfo, filteredInfo} = this;
        //        sortedInfo = sortedInfo || {};
        //        filteredInfo = filteredInfo || {};
        const columns = [{
            title: "销售结果",
            dataIndex: "status",
            key: "status"
          },
          {
            title: "销售说明",
            dataIndex: "report",
            key: "report"
          },
          {
            title: "记录事项",
            dataIndex: "remark",
            key: "remark"
          },
          {
            title: "预约时间",
            dataIndex: "preset_time",
            key: "preset_time",
            sorter: true
          },
          {
            title: "记录时间",
            dataIndex: "date_create",
            key: "date_create",
            sorter: true
          },
          {
            title: "记录人",
            dataIndex: "user_create",
            key: "user_create"
          },
          {
            title: "操作",
            key: "operation",
            dataIndex: "operation",
            scopedSlots: {
              customRender: "operation"
            }
          }
        ];
        return columns;
      }
    },
    methods: {
      ...mapActions(['setTrackRecordCondition']),
      onSelectChange(selectedRowKeys) {
        this.selectedRowId = [];
        for (let i = 0; i < selectedRowKeys.length; i++) {
          const target = this.trackRecordData.filter(
            item => item.key === selectedRowKeys[i]
          )[0];
          this.selectedRowId.push(target.id);
        }
        this.selectedRowKeys = selectedRowKeys;
      },
      getTrackRecordDataFromServer() {
        //TODO:AJAX获取，无接口情况在此处初始化
        let _this = this
        return new Promise((resolve, reject) => {
          getTrackRecordOfDetail(_this.currentCustInfo.id).then((result) => {
            _this.trackRecordData = []
            result.data.forEach((item,index) => {
              _this.trackRecordData.push({
                key:index,
                status: item.status,
                report: item.report,
                remark: item.remark,
                user_create: item.user_create,
                preset_time: item.preset_time,
                date_create: item.date_create
              })
            })
            resolve(result)
          }).catch((err) => {
            // _this.$myNotification.sendFailedNotification(err)
            reject(err)
          });
        })
      },
      getTrackRecordDataByParamsFromServer(params) {
        //TODO:AJAX获取，无接口情况在此处初始化
        let _this = this
        return new Promise((resolve, reject) => {
          getTrackRecordOfDetailByParams(_this.currentCustInfo.id,params).then((result) => {
            _this.trackRecordData = []
            result.data.forEach((item,index) => {
              _this.trackRecordData.push({
                key:index,
                status: item.status,
                report: item.report,
                remark: item.remark,
                user_create: item.user_create,
                preset_time: item.preset_time,
                date_create: item.date_create
              })
            })
            resolve(result)
          }).catch((err) => {
            reject(err)
          });
        })
      },
      handleChange(pagination, filters, sorter) {
        let _this = this
        let whereArr = [];
        this.loading = true
        // for (var key of Object.keys(filters)) {
        //   if (filters[key]) {
        //     whereArr.push({
        //       field: key,
        //       condition: "like",
        //       value: filters[key][0],
        //       link: "and"
        //     });
        //   }
        // }
        // //如果whereArr数组中全部元素的value都为空，则将whereArr清空
        // if (whereArr.length) {
        //   whereArr = whereArr.filter(item => item.value);
        // }
        // const params = {
        //   where: whereArr.length
        //     ? JSON.stringify(whereArr)
        //     : this.trackRecordCondition.where,
        //   orderby: sorter.columnKey ? sorter.columnKey : this.trackRecordCondition.orderby,
        //   order: sorter.order ? sorter.order : this.trackRecordCondition.order,
        //   page: pagination.current ? pagination.current : this.trackRecordCondition.page
        // };
        const params = {
          orderby: sorter.columnKey ? sorter.columnKey : this.trackRecordCondition.orderby,
          order: sorter.order ? sorter.order : this.trackRecordCondition.order,
          page: pagination.current ? pagination.current : this.trackRecordCondition.page
        };
        this.setTrackRecordCondition(params);
        this.filteredInfo = filters;
        this.sortedInfo = sorter;
        //翻页、筛选、排序时的请求操作
        this.getTrackRecordDataByParamsFromServer(params).then((result) => {
          _this.currentPage = result.current_page;
          _this.pageSize = result.per_page;
          _this.total = result.total;
          _this.loading = false
        }).catch((err) => {
          _this.loading = false
          _this.$myNotification.sendFailedNotification(err)
        });;
      },
      onDelete(key){
      }
    }
  };
</script>



