            customRender: (value, row, index) => {
              const obj = {
                children: value,
                attrs: {
                  class: "typeName"
                }
              };
              if (index === 0) {
                obj.attrs.rowSpan = 5;
              }
              if ((index > 0) & (index <= 4)) {
                obj.attrs.rowSpan = 0;
              }
              // These two are merged into above cell
              if (index === 5) {
                obj.attrs.rowSpan = 9;
              }
              if (index > 5) {
                obj.attrs.rowSpan = 0;
              }
              // These two are merged into above cell
              return obj;
            },
            width: 90
          },
