  methods: {
    getSelectedInsuredPrj() {
        let InsuredResult = [];
        let BUSINESS_ITEMS = []
        this.business = []
        InsuredResult = this.dataSource.filter(
          item => item.insuredPrj.prjIsChecked
        );
        InsuredResult.forEach(item => {
          if (item.insuranceAmount.type == "input_select") {
            this.business[item.insuranceAmount.select_code] = item.insuranceAmount.select_value
            this.business[item.insuranceAmount.input_code] = item.insuranceAmount.input_value
          } else if (item.insuranceAmount.type == "input_3") {
            this.business[item.insuranceAmount.code_one] = item.insuranceAmount.value_one
            this.business[item.insuranceAmount.code_two] = item.insuranceAmount.value_two
          } else {
            this.business[item.insuranceAmount.code] = item.insuranceAmount.value
          }
          this.business[item.noDeductiblePremium.code] = item.noDeductiblePremium.nDIsChecked
          BUSINESS_ITEMS.push(item.insuredPrj.code)
          if (item.noDeductiblePremium.nDIsChecked) {
            BUSINESS_ITEMS.push(item.noDeductiblePremium.code)
          }
        })
        this.business.push(BUSINESS_ITEMS)
        return this.business
      },
    //如果投保项目被勾选，不计免赔保费可以设置被勾选和不勾选。如果投保项目不勾选，不计免赔保费只能不勾选状态。
    onChange(index) {
      this.dataSource[Number(index) - 1].insuredPrj.prjIsChecked = !this
        .dataSource[Number(index) - 1].insuredPrj.prjIsChecked;
      if (this.dataSource[Number(index) - 1].insuredPrj.prjIsChecked) {
        this.dataSource[
          Number(index) - 1
        ].noDeductiblePremium.nDIsChecked = true;
      } else {
        this.dataSource[
          Number(index) - 1
        ].noDeductiblePremium.nDIsChecked = false;
      }
    },
    onNDChange(index) {
      if (this.dataSource[Number(index) - 1].insuredPrj.prjIsChecked) {
        this.dataSource[
          Number(index) - 1
        ].noDeductiblePremium.nDIsChecked = !this.dataSource[Number(index) - 1]
          .noDeductiblePremium.nDIsChecked;
      } else {
      }
    },
    onAmountInputChange(text,value){
      if(text.type=="input_select"){
        text.input_value = value
      }else{
        text.value = value
      }
    },
    onAmountSelectChange(text,value){
      if(text.type=="input_select"){
        text.select_value = value
      }else{
        text.value = value
      }
    },
    onInputOneChange(text,value){
      text.value_one = value
    },
    onInputTwoChange(text,value){
      text.value_two = value
    },
    equipmentInfo(key){
      this.$refs.equipmentInfo.showModal()
    }
  }
};
</script>

<style lang="less" scoped>
