 -->
<template>
  <a-form :form="form">
    <table class="cal-pri-info" border="0">
      <tr>
        <th>
        </th>
        <th>
          <a-form-item label='保司平台' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-select v-decorator="['insurance',{initialValue:((this.Calculate_List.filter(item => item.default)||[])[0]||{}).code||''}]"
              style="font-weight: normal;">
              <a-select-option v-for="(selectItem,index) in this.Calculate_List" :key="index" :value="selectItem.code">{{selectItem.name}}
              </a-select-option>
            </a-select>
          </a-form-item>
        </th>
        <th>
          <a-button icon="reload" :loading="refreshCalculateListLoading" @click="refreshCalculateList()" style="margin-left:5px;margin-bottom:0">刷新</a-button>
        </th>
        <th></th>
        <th class="calculate-price-btn">
          <slot name="calculateBtn" />
        </th>
      </tr>
      <tr>
        <td class="typeName">被保险人信息</td>
        <td>
          <a-form-item label='证件号' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['HOLDER_IDENTIFY_NO',{initialValue:data.HOLDER_IDENTIFY_NO}]" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='被保险人' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['HOLDER',{initialValue:data.HOLDER}]" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='手机号码' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['MOBILE',{initialValue:data.MOBILE}]">
              <a-icon slot="suffix" style="cursor:pointer" type="phone" @click="e=>zswitch_callcenter_click_call_b(this.form.getFieldValue('MOBILE'))" />
            </a-input>
          </a-form-item>
        </td>
        <td></td>
      </tr>
      <tr>
        <td class="typeName" rowspan="4">车辆信息</td>
        <td>
          <a-form-item label='车牌号码' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['LICENSE_NO',{initialValue:data.LICENSE_NO}]" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='车牌类型' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-select v-decorator="['LICENSE_TYPE',{initialValue:data.LICENSE_TYPE}]">
              <a-select-option key="小型汽车号牌" value="小型汽车号牌">小型汽车号牌
              </a-select-option>
              <a-select-option key="大型汽车号牌" value="大型汽车号牌">大型汽车号牌
              </a-select-option>
              <a-select-option key="挂车号牌" value="挂车号牌">挂车号牌
              </a-select-option>
              <a-select-option key="使馆汽车号牌" value="使馆汽车号牌">使馆汽车号牌
              </a-select-option>
              <a-select-option key="领事馆汽车号牌" value="领事馆汽车号牌">领事馆汽车号牌
              </a-select-option>
            </a-select>
          </a-form-item>
        </td>
        <td>
          <a-form-item label='拥有人' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['OWNER',{initialValue:data.OWNER}]" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='VIN' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['VIN_NO',{initialValue:data.VIN_NO}]" />
          </a-form-item>
        </td>
      </tr>
      <tr>
        <td>
          <a-form-item label='发动机号' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['ENGINE_NO',{initialValue:data.ENGINE_NO}]" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='品牌型号' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['MODEL',{initialValue:data.MODEL}]" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='排量' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['ENGINE',{initialValue:data.ENGINE}]" addonAfter="L" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='核定载客' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['SEATS',{initialValue:data.SEATS}]" />
          </a-form-item>
        </td>
      </tr>
      <tr>
        <td>
          <a-form-item label='整备质量' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['KERB_MASS',{initialValue:data.KERB_MASS}]" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='型号代码' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['MODEL_CODE',{initialValue:data.MODEL_CODE}]" />
          </a-form-item>
        </td>
        <td style="display:flex;align-items:center">
          <a-form-item label='购置价' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-decorator="['BUYING_PRICE',{initialValue:data.BUYING_PRICE}]" addonAfter="元" />
          </a-form-item>
          <a-button type="primary" @click="queryVehicleType()" style="margin-left:5px;margin-bottom:0">查车型</a-button>
        </td>
        <td>
          <a-form-item label='注册日期' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-date-picker v-decorator="['ENROLL_DATE',{initialValue:data.ENROLL_DATE}]" @change="onChange" />
          </a-form-item>
        </td>
      </tr>
      <tr>
        <td>
          <a-form-item label='车辆种类' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-select v-decorator="['VEHICLE_TYPE',{initialValue:data.VEHICLE_TYPE}]">
              <a-select-option key="客车" value="客车">客车
              </a-select-option>
              <a-select-option key="货车" value="货车">货车
              </a-select-option>
              <a-select-option key="推土机" value="推土机">推土机
              </a-select-option>
            </a-select>
          </a-form-item>
        </td>
        <td>
          <a-form-item label='使用性质' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-select v-decorator="['USE_CHARACTER',{initialValue:data.USE_CHARACTER}]">
              <a-select-option key="家庭自用汽车" value="家庭自用汽车">家庭自用汽车
              </a-select-option>
              <a-select-option key="非营业企客车" value="非营业企客车">非营业企客车
              </a-select-option>
            </a-select>
          </a-form-item>
        </td>
        <td>
          <a-form-item label='产地' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-select v-decorator="['ORIGIN',{initialValue:data.ORIGIN}]">
              <a-select-option key="国产" value="国产">国产
              </a-select-option>
              <a-select-option key="进口" value="进口">进口
              </a-select-option>
              <a-select-option key="合资" value="合资">合资
              </a-select-option>
            </a-select>
          </a-form-item>
        </td>
        <td></td>
      </tr>
      <tr>
        <td class="typeName">期限</td>
        <td colspan="2">
          <a-form-item label='交强险' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <range-picker v-decorator="['MVTALCI_TIME',{initialValue:[moment(data.MVTALCI_TIME[0]),moment(data.MVTALCI_TIME[1])]}]"
              :ranges="{ Today: [moment(), moment()], 'This Month': [moment(), moment().endOf('month')],'This year':[moment(),moment().add(12,'month')] }"
              format="YYYY-MM-DD HH:mm:ss" showTime @change="onMvtalciChange" />
          </a-form-item>
        </td>
        <td colspan="2">
          <a-form-item label='商业险' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <range-picker v-decorator="['BUSINESS_TIME',{initialValue:[moment(data.BUSINESS_TIME[0]),moment(data.BUSINESS_TIME[1])]}]"
              :ranges="{ Today: [moment(), moment()], 'This Month': [moment(), moment().endOf('month')],'This year':[moment(),moment().add(12,'month')] }"
              format="YYYY-MM-DD HH:mm:ss" showTime @change="onBusinessChange" />
          </a-form-item>
        </td>
      </tr>
      <tr class="calculateResult">
        <td class="typeName">交强险摘要</td>
        <td>
          <a-form-item label='保费' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.MVTALCI_PREMIUM" addonAfter="元" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='保司折扣' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.MVTALCI_DISCOUNT" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='车船税' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.TRAVEL_TAX_PREMIUM" addonAfter="元" />
          </a-form-item>
        </td>
        <td></td>
      </tr>
      <tr class="calculateResult">
        <td class="typeName">商业险摘要</td>
        <td>
          <a-form-item label='商业险合计' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.BUSINESS_PREMIUM" addonAfter="元" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='保司折扣' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.BUSINESS_DISCOUNT" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='自定折扣' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input v-model="data.BUSINESS_CUSTOM_DISCOUNT" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='折后合计' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.BUSINESS_DISCOUNT_PREMIUM" addonAfter="元" />
          </a-form-item>
        </td>
      </tr>
      <tr class="calculateResult">
        <td class="typeName">费用总览</td>
        <td>
          <a-form-item label='保费合计' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.TOTAL_PREMIUM" addonAfter="元" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='折后商业险' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.TOTAL_BUSINESS_PREMIUM" addonAfter="元" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='交强险' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.TOTAL_MVTALCI_PREMIUM" addonAfter="元" />
          </a-form-item>
        </td>
        <td>
          <a-form-item label='不计免赔' :labelCol="{ span: 5 }" :wrapperCol="{ span: 12 }">
            <a-input :value="data.TVDI_NDSI_PREMIUM" addonAfter="元" />
          </a-form-item>
        </td>
      </tr>
    </table>
    <query-model ref="queryModel" @getModel="getModel" :params="params" />
  </a-form>
</template>

<script>
import Vue from "vue";
import AForm from "ant-design-vue/lib/form";
import AInput from "ant-design-vue/lib/input";
import ASelect from "ant-design-vue/lib/select";
import AButton from "ant-design-vue/lib/button";
import ADatePicker from "ant-design-vue/lib/date-picker";
import AIcon from "ant-design-vue/lib/icon";
import QueryModel from "./QueryModel.vue";
import moment from "moment";
import { mapGetters } from "vuex";
import { getCalculateList } from "@/api/calculateSetting.js";
import { getDetailCustomer } from "@/api/accounts.js";
const ASelectOption = ASelect.Option;
const AFormItem = AForm.Item;
const { RangePicker } = ADatePicker;

const renderContent = (value, row, index) => {
  const obj = {
    children: value,
    attrs: {}
  };
  return obj;
};
export default {
  props: {
    id:{
      type:String,
      default:'0'
    }
  },
  components: {
    AIcon,
    AForm,
    AInput,
    ASelect,
    AButton,
    ASelectOption,
    AFormItem,
    ADatePicker,
    RangePicker,
    QueryModel
  },
  data() {
    return {
      data: {},
      Calculate_List: Vue.ls.get("Calculate_List") || [],
      params: {}, //查询车型的参数
      refreshCalculateListLoading: false,
      ID_FieldMap: {    
        id_code: "HOLDER_IDENTIFY_NO",
        owner: "OWNER",
        mobile: "MOBILE",
        plate_no: "LICENSE_NO",
        vin: "VIN_NO",
        engine_no: "ENGINE_NO",
        model: "MODEL",
        engine: "ENGINE",
        seats: "SEATS",
        purchase_price: "BUYING_PRICE",
        register_date: "ENROLL_DATE",
        vehicle_type: "VEHICLE_TYPE",
        use_character: "USE_CHARACTER"
      },
      fieldMap: {
        id: "MODEL_CODE", //型号代码
        name: "MODEL", //品牌型号
        purchasePrice: "BUYING_PRICE", //购置价
        seats: "SEATS", //核定载客
        weight: "KERB_MASS", //整备质量
        class: "VEHICLE_TYPE", //车辆种类
        displacement: "ENGINE", //排量
        production: "ORIGIN" //产地
      },
      business: []
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  },
  created() {
    let _this = this;
    this.getCustomerInfoFromServer();
    if (!this.Calculate_List.length) {
      this.getCalculateListFromServer().then(result => {
      });
    }
    this.getCustomerInfoDataFromServer(this.$props.id).then(
      result => {}
    );
  },
  computed: {
    ...mapGetters(["currentCustInfo"])
  },
  watch: {
    data: {
      handler: function() {
        this.params = {
          insurance: this.form.getFieldValue("insurance"),
          model: this.form.getFieldValue("MODEL"),
          vin: this.form.getFieldValue("VIN"),
          modelCode: this.form.getFieldValue("MODEL_CODE")
        };
      }
    }
  },
  methods: {
    updateCalPrice(){
      let _this = this
      this.spinning = true;
      this.getCustomerInfoFromServer();
      this.getCustomerInfoDataFromServer(this.$props.id).then(result => {
          _this.form.resetFields()
          _this.spinning = false;
        })
        .catch(err => {
          _this.spinning = false;
        });
    },
    getCustomerInfoDataFromServer(id) {
      let _this = this;
      return new Promise((resolve, reject) => {
        getDetailCustomer(id)
          .then(result => {
            result.items.forEach(item => {
              item.data.forEach(itemChild => {
                itemChild.options = "";
                // type 为 date 日期格式需要强制转化为 moment 格式
                if (itemChild.type == "date" || itemChild.type == "datetime") {
                  itemChild.value =
                    itemChild.value != "" ? moment(itemChild.value) : null;
                }
                //由于接口获取到的字符串和实际渲染使用、接口传参使用的字符串不同，所以通过下面来实现转换
                if (_this.ID_FieldMap[itemChild.field]) {
                  _this.data[_this.ID_FieldMap[itemChild.field]] =
                    itemChild.value;
                }
              });
            });
            resolve(result);
          })
          .catch(err => {
            _this.$myNotification.sendFailedNotification(err);
            reject(err);
          });
      });
    },
    getCustomerInfoFromServer() {
      //TODO:在此进行AJAX请求，获取算价器页面的客户信息
      this.data = {
        NO: "",
        insurance: "",
        HOLDER_IDENTIFY_NO: "",
        HOLDER: "",
        MOBILE: "",
        LICENSE_NO: "",
        LICENSE_TYPE: "小型汽车号牌",
        OWNER: "",
        VIN_NO: "",
        ENGINE_NO: "",
        MODEL: "",
        ENGINE: "0",
        SEATS: "0",
        KERB_MASS: "0",
        MODEL_CODE: "",
        BUYING_PRICE: "0",
        ENROLL_DATE: null,
        VEHICLE_TYPE: "客车",
        USE_CHARACTER: "",
        ORIGIN: "国产",
        MVTALCI_TIME: ["2018-10-12 00:00:00", "2019-10-11 23:59:59"],
        BUSINESS_TIME: ["2018-10-12 00:00:00", "2019-10-11 23:59:59"],
        MVTALCI_PREMIUM: "0.00",
        MVTALCI_DISCOUNT: "1.0000",
        TRAVEL_TAX_PREMIUM: "0.00",
        BUSINESS_PREMIUM: "0.00",
        BUSINESS_DISCOUNT: "1.0000",
        BUSINESS_CUSTOM_DISCOUNT: "1.0000",
        BUSINESS_DISCOUNT_PREMIUM: "1.0000",
        TOTAL_PREMIUM: "0.00",
        TOTAL_BUSINESS_PREMIUM: "0.00",
        TOTAL_MVTALCI_PREMIUM: "0.00",
        TVDI_NDSI_PREMIUM: "0.00"
      };
    },
    moment,
    getModel(model) {
      this.form.setFieldsValue({ MODEL_CODE: model["id"] });
      this.form.setFieldsValue({ MODEL: model["name"] });
      this.form.setFieldsValue({ BUYING_PRICE: model["purchasePrice"] });
      this.form.setFieldsValue({ SEATS: model["seats"] });
      this.form.setFieldsValue({ KERB_MASS: model["weight"] });
      this.form.setFieldsValue({ VEHICLE_TYPE: model["class"] });
      this.form.setFieldsValue({ ENGINE: model["displacement"] });
      this.form.setFieldsValue({ ORIGIN: model["production"] });
      // for(let key in this.fieldMap){
      //   this.form.setFieldsValue({this.fieldMap[key]:model[key]})
      // }
    },
    zswitch_callcenter_click_call_b(phone_number) {
      window.location.href = "sip:" + phone_number;
    },
    getCalculateListFromServer() {
      let _this = this;
      return new Promise((resolve, reject) => {
        getCalculateList()
          .then(result => {
            Vue.ls.set("Calculate_List", result.data, 7 * 24 * 60 * 60 * 1000);
            resolve(result);
          })
          .catch(err => {
            _this.$myNotification.sendFailedNotification(err);
            reject(err);
          });
      });
    },
    refreshCalculateList() {
      let _this = this;
      this.refreshCalculateListLoading = true;
      this.getCalculateListFromServer()
        .then(result => {
          _this.Calculate_List = result.data;
          _this.refreshCalculateListLoading = false;
        })
        .catch(err => {
          _this.refreshCalculateListLoading = false;
        });
    },
    queryVehicleType() {
      this.$refs.queryModel.showModal();
    },
    onChange(value, dataString) {
      this.form.setFieldsValue({
        ENROLL_DATE: value
      });
      // this.data[16].value=dataString
    },
    onMvtalciChange(value, dataString) {
      this.form.setFieldsValue({
        MVTALCI_TIME: value
      });
    },
    onBusinessChange(value, dataString) {
      this.form.setFieldsValue({
        BUSINESS_TIME: value
      });
    },

    submit() {
      let _this = this;
      this.form.validateFields((err, values) => {
        // let result="综合信息："+_this.data+",投保需求："+this.getSelectedInsuredPrj();
        if (!err) {
          values.ENROLL_DATE = values.ENROLL_DATE
            ? values.ENROLL_DATE.format("YYYY-MM-DD")
            : "";
          values.MVTALCI_TIME = values.MVTALCI_TIME
            ? [
                values.MVTALCI_TIME[0].format("YYYY-MM-DD HH:mm:ss"),
                values.MVTALCI_TIME[1].format("YYYY-MM-DD HH:mm:ss")
              ]
            : "";
          values.BUSINESS_TIME = values.BUSINESS_TIME
            ? [
                values.BUSINESS_TIME[0].format("YYYY-MM-DD HH:mm:ss"),
                values.BUSINESS_TIME[1].format("YYYY-MM-DD HH:mm:ss")
              ]
            : "";
        }
      });
    },
    quickQuotation(e) {}
  }
};
</script>

<style>
</style>



