<template>
  <div class="cal-price-history">
    <div style="
    display: flex;
    line-height: 32px;"
    >
    <h4 class="title" style="flex:1;color: #000;font-size:14px">{{ title }}</h4>
    </div>
    <a-row class="list">
      <a-col 
        class="li" 
        :key="index" 
        v-for="(record, index) in list"
        :xxl="24/defaultNum" :xl="24/(defaultNum-1)" :lg="24/(defaultNum-1)" :md="24/defaultNum" :sm="24/(defaultNum-1)" :xs="24"
        :style="{ display: index < count ? 'flex' : 'none' ,cursor: 'pointer'}"
       >
        <slot :record="record" :index="index"></slot>
        
      </a-col>
    </a-row>
    <a-row>
      <a-col
        :span="24"
        :style="{ textAlign: 'right' }"
      >
        <a
          :style="{ marginLeft: '8px', fontSize: '14px' }"
          @click="toggle"
        >
          {{ expand ? '收起' : '展开' }}
          <a-icon :type="expand ? 'up' : 'down'" />
        </a>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import ARow from 'ant-design-vue/lib/row'
import ACol from 'ant-design-vue/lib/col'
import AIcon from 'ant-design-vue/lib/icon'
import AButton from 'ant-design-vue/lib/button'
import {mixinDevice} from '@/utils/mixin.js'
  export default {
    name: 'RankList',
    // ['title', 'list']
    props: {
      title: {
        type: String,
        default: ''
      },
      list: {
        type: Array,
        default: null
      },
      defaultNum: {
        type: Number,
        default: 3
      }
    },
    mixins:[mixinDevice],
    components:{
      ARow,
      ACol,
      AIcon,
      AButton
    },
    data(){
      return{
        expand: false
      }
    },
    computed: {
      count() {
        return this.expand ? this.list.length : this.$props.defaultNum;
      }
    },
    methods: {
    toggle() {
      this.expand = !this.expand;
    }
  }
  }
</script>

<style lang="scss">

  .cal-price-history {
    padding: 16px 24px;
    background: #fbfbfb;
    margin-bottom: 16px;
    h4.title {
      padding: 0;
    }
    .list {
      margin: 0;
      padding: 0;
      list-style: none;

      .li {
        margin-top: 16px;

        >span {
          background-color: #f5f5f5;
          border-radius: 20px;
          display: inline-block;
          font-size: 14px;
          font-weight: 600;
          margin-right: 24px;
          height: 20px;
          line-height: 20px;
          width: 20px;
          text-align: center;
          &.active {
              background-color: #314659;
              color: #fff;
            }
        }
         div{
            span{
              &:first-child {
                width: 100px;
              display: inline-block;
              font-size: 14px;
              font-weight: 600;
              margin-right: 24px;
              line-height: 20px;
              
            }
            &:last-child {
              float: right;
            }
            }
          }
      }
    }
  }

  // .mobile .cal-price-history {
  //   padding: 0 32px 32px 32px;
  // }

</style>
<style>
@media (max-width: 1089px) and (min-width:991px) {
  .list div.li{
    width: 33.33%
  }
}
</style>



