<template>
  <div class="equipment-info">
    <a-modal title="设备信息" :visible="visible" :maskClosable="maskClosable" @ok="handleOk" :confirmLoading="confirmLoading"
      @cancel="handleCancel" width="908px">
      <form :form="form" @submit="handleSubmit">
        <a-button style="width: 100%; margin-top: 8px; margin-bottom: 16px" type="dashed" icon="plus" @click="newMember">新增设备</a-button>
        <a-table class="equipment-info-table" :columns="columns" :dataSource="data" :pagination="pagination">
          <template slot="NAME" slot-scope="text, record, index">
            <a-form-item :validate-status="!data[index]['nameHasValue']?'error':''">
              <a-input v-decorator="[`NAME_${index}`]" style="margin: -5px 0;min-width:90px;max-width:127px"
                @change="e => handleChange(e, record.key, `NAME_${index}`)" />
            </a-form-item>
          </template>
          <template v-for="(col, i) in ['BUYING_PRICE','COUNT']" :slot="col" slot-scope="text, record, index">
            <a-form-item>
            <a-input v-decorator="[col+'_'+index,{initialValue:text}]" style="margin: -5px 0;min-width:100px;max-width:127px" :placeholder="columns[i].title"
              @change="e => handleChange(e, record.key, col+'_'+index)" />
            </a-form-item>
          </template>

          <template slot="BUYING_DATE" slot-scope="text, record, index">
            <a-form-item :validate-status="!data[index]['timeHasValue']?'error':''">
              <a-date-picker v-decorator="[`BUYING_DATE_${index}`]" style="margin: -5px 0;min-width:90px;max-width:127px"
                @change="value => handleDateChange(value, record.key, `BUYING_DATE_${index}`)" />
            </a-form-item>
          </template>

          <template slot="DEPRECIATION" slot-scope="text, record, index">
            <template>{{ text }}</template>
          </template>

          <template slot="operation" slot-scope="text, record, index">
                <span>
                <a @click="saveRow(record.key)">计算</a>
                <a-divider type="vertical" />
                <a-popconfirm title="是否要删除此行？" @confirm="remove(record.key)">
                    <a>删除</a>
                </a-popconfirm>
                </span>
          </template>
        </a-table>
      </form>
    </a-modal>
  </div>
</template>

<script>
import AModal from "ant-design-vue/lib/modal";
import ATable from "ant-design-vue/lib/table";
import AButton from "ant-design-vue/lib/button";
import AIcon from "ant-design-vue/lib/icon";
import AForm from "ant-design-vue/lib/form";
import AInput from "ant-design-vue/lib/input";
import ADivider from "ant-design-vue/lib/divider";
import APopconfirm from "ant-design-vue/lib/popconfirm";
import ATooltip from "ant-design-vue/lib/tooltip";
import ADatePicker from "ant-design-vue/lib/date-picker";
import moment from "moment";
import {
  addInsuranceAccount,
  editInsuranceAccount
} from "@/api/calculateSetting";
const AFormItem = AForm.Item;
export default {
  props: {
    activeCode: String
  },
  components: {
    AModal,
    ATable,
    AButton,
    AIcon,
    AForm,
    AFormItem,
    AInput,
    ADivider,
    APopconfirm,
    ATooltip,
    ADatePicker
  },
  data() {
    return {
      maskClosable: false, //点击蒙层不关闭modal
      visible: false,
      confirmLoading: false,
      pagination: {
        showQuickJumper: true,
        pageSize: 5
      },

      // table
      columns: [
        {
          title: "设备名称",
          dataIndex: "NAME",
          key: "NAME",
          scopedSlots: {
            customRender: "NAME"
          }
        },
        {
          title: "新购价格(元)",
          dataIndex: "BUYING_PRICE",
          key: "BUYING_PRICE",
          scopedSlots: {
            customRender: "BUYING_PRICE"
          }
        },
        {
          title: "数量",
          dataIndex: "COUNT",
          key: "COUNT",
          scopedSlots: {
            customRender: "COUNT"
          }
        },
        {
          title: "购买日期",
          dataIndex: "BUYING_DATE",
          key: "BUYING_DATE",
          scopedSlots: {
            customRender: "BUYING_DATE"
          }
        },
        {
          title: "折旧价(元)",
          dataIndex: "DEPRECIATION",
          key: "DEPRECIATION",
          scopedSlots: {
            customRender: "DEPRECIATION"
          }
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          scopedSlots: {
            customRender: "operation"
          }
        }
      ],
      data: []
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  },
  methods: {
    moment,
    // editInsuranceAccountToServer(account){
    //   let _this = this
    //   return new Promise((resolve,reject)=>{
    //     debugger
    //     editInsuranceAccount(account).then((result) => {
    //       resolve(result)
    //     }).catch((err) => {
    //       debugger
    //       reject(err)
    //     });
    //   })
    // },
    // addInsuranceAccountToServer(account){
    //   let _this = this
    //   return new Promise((resolve,reject) =>{
    //     addInsuranceAccount(account).then((result) => {
    //       debugger
    //       resolve(result)
    //     }).catch((err) => {
    //       _this.$myNotification.sendFailedNotification(err)
    //       reject(err)
    //     });
    //   })
    // },
    showModal() {
      this.visible = true;
    },
    handleOk(e) {
      let _this = this;
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
      }, 2000);
      //重置
    },
    handleCancel(e) {
      this.visible = false;
    },

    handleSubmit(e) {
      e.preventDefault();
      this.validate();
    },
    newMember() {
      const newData = {
        key: this.data.length + 1, //此处key暂时使用目前data的长度,待正式集成接口，data只会接收到当前页的数据，所以应使用所有数据的数量
        NAME: "",
        BUYING_PRICE: "0.00",
        COUNT: "1",
        BUYING_DATE: null,
        DEPRECIATION: "0.00",
        timeHasValue:true,
        nameHasValue:true
        // editable: true,
        // isNew: true
      };
      this.data.unshift(newData);
    },
    remove(key) {
      const newData = this.data.filter(item => item.key !== key);
      this.data = newData;
    },
    saveRow(key) {
      let _this = this
      const target = this.data.filter(item => item.key === key)[0];
      target.timeHasValue = target.BUYING_DATE?true:false
      if(!target.timeHasValue){
        this.$notification['warning']({
                  message: '请填写提示信息',
                })
        return 
      }
      this.form.validateFields((err,value)=>{
          if(!err){
              target.editable = false;
            target.isNew = false;
          }
          
      })
      
      //   if(target.isNew){
      //     this.addInsuranceAccountToServer(account).then((result) => {
      //       target.editable = false;
      //       target.isNew = false;
      //     }).catch((err)=>{
      //       // this.data = this.data.filter(item => key !== item.key)
      //     })
      //   }else{
      //     debugger
      //     this.editInsuranceAccountToServer(account).then((result) => {
      //       target.editable = false
      //       debugger
      //     }).catch((err) => {
      //       debugger
      //     });
      //   }
    },
    // toggle(key) {
    //   const target = this.data.filter(item => item.key === key)[0];
    //   target.editable = !target.editable;
    //   //   this.data.sort((a,b)=>{return b-a}) //此处犹豫出现未知问题，当点击编辑按钮时需要刷新this.data才能使组件重新渲染成编辑状态，所以暂时用此方法
    //   // this.data[key].editable = !this.data[key].editable
    // },
    // getRowByKey(key, newData) {
    //   const data = this.data;
    //   return (newData || data).filter(item => item.key === key)[0];
    // },
    // cancel(key) {
    //   const target = this.data.filter(item => item.key === key)[0];
    //   target.editable = false;
    //   //   this.data.sort((a,b)=>{return b-a}) //此处犹豫出现未知问题，当点击取消编辑按钮时需要刷新this.data才能使组件重新渲染成查看状态，所以暂时用此方法
    // },
    handleChange(e, key, column) {
      const value = e.target.value
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    handleDateChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    }
  }
};
</script>

<style>
div.equipment-info-table tbody tr td{
    min-width:100px;
    max-width:127px;
    overflow: hidden;
    text-overflow: ellipsis;
}
div.equipment-info-table tbody tr td div.ant-form-explain{
  display:none
}
div.equipment-info-table tbody tr td>div.ant-form-item{
  margin-bottom:0
}
</style>




