<template>
  <div class="trackRecordModal">
    <a-modal title="跟踪记录" :visible="visible" :maskClosable="maskClosable" @ok="handleOk" :confirmLoading="confirmLoading" @cancel="handleCancel">
      <a-form :form="form" class="track-record-modal-form">
        <a-form-item label="意向程度">
          <a-select placeholder="请选择" v-decorator="['intention',{initialValue:'HIGH'}]">
            <a-select-option value="HIGH">高</a-select-option>
            <a-select-option value="MIDDLE">中</a-select-option>
            <a-select-option value="LOW">低</a-select-option>
          </a-select>
        </a-form-item>
        <!-- <a-row class="track-record-row intention">
              <a-col class="gutter-row" :xl="6" :lg="10">
                <span>意向程度</span>
              </a-col>
              <a-col class="gutter-row" :xl="18" :lg="14">
                
              </a-col>
            </a-row> -->
        <a-form-item label="销售结果">
          <a-select placeholder="请选择" v-decorator="['status',{initialValue:'APPOINTMENT_QUOTATION'}]">
            <a-select-option value="APPOINTMENT_QUOTATION">预约已报价</a-select-option>
            <a-select-option value="APPOINTMENT_NON_QUOTATION">预约未报价</a-select-option>
            <a-select-option value="FAILED">失败</a-select-option>
            <a-select-option value="INVALID">无效</a-select-option>
            <a-select-option value="SUCCESS">成功</a-select-option>
          </a-select>
        </a-form-item>
        <!-- <a-row class="track-record-row track-record-row status">
              <a-col class="gutter-row" :xl="6" :lg="10">
                <span>
                  销售结果
                </span>
              </a-col>
              <a-col class="gutter-row" :xl="18" :lg="14">
                <a-select defaultValue="APPOINTMENT_QUOTATION" style="width: 120px">
                  <a-select-option value="APPOINTMENT_QUOTATION">预约已报价</a-select-option>
                  <a-select-option value="APPOINTMENT_NON_QUOTATION">预约未报价</a-select-option>
                  <a-select-option value="FAILED">失败</a-select-option>
                  <a-select-option value="INVALID">无效</a-select-option>
                  <a-select-option value="SUCCESS">成功</a-select-option>
                </a-select>
              </a-col>
            </a-row> -->
        <a-form-item label="销售说明">
          <a-select placeholder="请选择" v-decorator="['client_detailview_value_1']">
            <!-- <a-select-option value="">高</a-select-option> -->
            <!-- <a-select-option value="MIDDLE">中</a-select-option>
                  <a-select-option value="LOW">低</a-select-option> -->
          </a-select>
        </a-form-item>
        <!-- <a-row class="track-record-row client_detailview_value_1">
              <a-col class="gutter-row" :xl="6" :lg="10">
                <span>销售说明</span>
              </a-col> -->
        <!--<a-select defaultValue="" style="width: 120px">-->
        <!--<a-select-option value=""> </a-select-option>-->
        <!--</a-select>-->
        <!-- </a-row> -->
        <a-form-item label="预约时间">
          <a-date-picker v-decorator="['preset_time']" style="width:100%" showTime format="YYYY-MM-DD HH:mm:ss"/>
        </a-form-item>
        <!-- <a-row class="track-record-row preset_time">
              <a-col class="gutter-row" :xl="6" :lg="10">
                <span>预约时间</span>
              </a-col>
              <a-col class="gutter-row" :xl="18" :lg="14">
                <a-date-picker showTime/>
              </a-col>
            </a-row> -->
        <a-form-item label="备注">
          <a-textarea v-decorator="['remark']" :autosize="{ minRows: 2, maxRows: 6 }" />
        </a-form-item>
        <!-- <a-row class="track-record-row remark">
              <a-col class="gutter-row" :xl="6" :lg="10">
                <span>备注</span>
              </a-col>
              <a-col class="gutter-row" :xl="18" :lg="14">
                
              </a-col>
            </a-row> -->
      </a-form>
    </a-modal>
  </div>
</template>

<script>
  import AModal from 'ant-design-vue/lib/modal'
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import ASelect from 'ant-design-vue/lib/select'
  import AInput from 'ant-design-vue/lib/input'
  import ADatePicker from 'ant-design-vue/lib/date-picker'
  import AForm from 'ant-design-vue/lib/form'
  import {
    addTrackRecord
  } from '@/api/trackRecord'
  import {mixin} from '@/utils/mixin'
  const ASelectOption = ASelect.Option
  const ATextarea = AInput.TextArea
  const AFormItem = AForm.Item
  
  export default {
    props:{
      ID:{
        type:String,
        default:''
      }
    },
    components: {
      AModal,
      ARow,
      ACol,
      ASelect,
      ASelectOption,
      AInput,
      ATextarea,
      ADatePicker,
      AForm,
      AFormItem
    },
    mixins: [mixin],
    data() {
      return {
        maskClosable: false,
        ModalText: 'Content of the modal',
        visible: false,
        confirmLoading: false,
      }
    },
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    methods: {
      handleSubmit() {
        let _this = this;
        return new Promise((resolve, reject) => {
          _this.form.validateFields((err, values) => {
            if (!err) {
              for (let item in values) {
                if (values[item]) {
                  if (item == "preset_time") {
                    values[item] = values[item] ? values[item].format('YYYY-MM-DD HH:mm:ss') : ""
                  }
                } else {
                    delete values[item]
                }
              }
              values.accountid = _this.$props.ID
              addTrackRecord(values).then((result) => {
                resolve(result)
                _this.visible = false;
                _this.confirmLoading = false;
              }).catch((err) => {
                reject(err)
                _this.confirmLoading = false;
              });
            }
          })
          // resolve(true)
        });
      },
      showModal() {
        //        debugger;
        this.visible = true
      },
      handleOk(e) {
        this.confirmLoading = true;
        this.handleSubmit()
      },
      handleCancel(e) {
        this.visible = false
      },
    }
  }
</script>

<style>
  .track-record-modal-form div.ant-form-item-label {
    width: 90px;
  }
</style>



