<template>
  <div class="cal-price-history">
    <a-row style="margin-left: -12px;margin-right: -12px;">
        <a-col :sm="24" :md="12" :xl="6" v-for="(item, i) in histories" :key="i" style="padding-left: 12px;padding-right: 12px;margin-bottom: 24px;cursor: pointer">
        <list-card :loading="false" :title="item.nickname" :description="item.description" :time="'2019-02-21 08:12:34'">
            <!-- <a-tooltip title="中国人保" slot="action"> -->
                <!-- <a-avatar size="small" src="http://www.epicc.com.cn/m/all_file/img/app-logo.png" /> -->
                <a-avatar slot="action" style="color: #f56a00; backgroundColor: #fde3cf">PICC</a-avatar>
            <!-- </a-tooltip> -->
        </list-card>
        </a-col>
        <a-col :sm="24" :md="12" :xl="6" style="padding-left: 12px; padding-right: 12px; margin-bottom: 24px; cursor: pointer">
        <a-card :loading="false" :bordered="false" :body-style="{ padding: '0' }" >
            <a-button class="card-btn" style="border-radius:10px;margin-bottom:0">
            更多算价记录
            </a-button>
        </a-card>
        </a-col>
    </a-row>
  </div>
</template>

<script>
import ARow from 'ant-design-vue/lib/row'
import ACol from 'ant-design-vue/lib/col'
import AIcon from 'ant-design-vue/lib/icon'
import AButton from 'ant-design-vue/lib/button'
import ATooltip from 'ant-design-vue/lib/tooltip'
import ListCard from '@/components/Card/ListCard'
import ACard from 'ant-design-vue/lib/card'
import AAvatar from 'ant-design-vue/lib/avatar'
import { getTodayStar } from '@/api/workplace.js'
  export default {
    name: 'calculatePriceHistory',
    // ['title', 'list']
    components:{
      ARow,
      ACol,
      AIcon,
      AButton,
      ATooltip,
      ACard,
      AAvatar,
      ListCard
    },
    data(){
      return{
        histories: [],
        advanced:false
      }
    },
    created(){
        this.getTodayStarFromServer()
    },
    computed: {
    },
    methods: {
    toggleAdvanced(){
        this.advanced = !this.advanced
      },
      getTodayStarFromServer() {
        let _this = this
        getTodayStar()
          .then(res => {
            _this.histories = res.data.slice(0,3)
            _this.starLoading = false
          }).catch((err) => {
            _this.starLoading = false
          })
      },
  }
  }
</script>

<<style lang="scss" scoped>
  .history-list {

    .card-title {
      font-size: 0;

      a {
        color: rgba(0, 0, 0, 0.85);
        margin-left: 12px;
        line-height: 24px;
        height: 24px;
        display: inline-block;
        vertical-align: top;
        font-size: 14px;

        &:hover {
          color: #1890ff;
        }
      }
    }
    .card-description {
      color: rgba(0, 0, 0, 0.45);
      height: 44px;
      line-height: 22px;
      overflow: hidden;
    }
    .history-item {
      display: flex;
      margin-top: 8px;
      overflow: hidden;
      font-size: 12px;
      height: 20px;
      line-height: 20px;
      a {
        color: rgba(0, 0, 0, 0.45);
        display: inline-block;
        flex: 1 1 0;

        &:hover {
          color: #1890ff;
        }
      }
      .datetime {
        color: rgba(0, 0, 0, 0.25);
        flex: 0 0 auto;
        float: right;
        cursor: pointer
      }
    }
    .ant-card-meta-description {
      color: rgba(0, 0, 0, 0.45);
      height: 44px;
      line-height: 22px;
      overflow: hidden;
    }
    
  }

  .item-group {
    a {
      color: rgba(0, 0, 0, 0.65);
      display: inline-block;
      font-size: 14px;
      margin-bottom: 13px;
      width: 25%;
    }
  }

  .members {
    a {
      display: block;
      margin: 12px 0;
      line-height: 24px;
      height: 24px;
      .member {
        font-size: 14px;
        color: rgba(0, 0, 0, .65);
        line-height: 24px;
        max-width: 100px;
        vertical-align: top;
        margin-left: 12px;
        transition: all 0.3s;
        display: inline-block;
      }
      &:hover {
        span {
          color: #1890ff;
        }
      }
    }
  }

  .mobile {

    .history-list {

      .history-card-grid {
        width: 100%;
      }
    }

    .more-info {
      border: 0;
      padding-top: 16px;
      margin: 16px 0 16px;
    }

    .headerContent .title .welcome-text {
      display: none;
    }
  }
  .card-btn{
    width: 100%;
    height: 108px;
    border: 2px dashed #d9d9d9;
  }
</style>


