<template>
    <div class="recordModal">
        <a-modal title="录音" :visible="visible" :maskClosable="maskClosable" @ok="handleOk" :confirmLoading="confirmLoading" @cancel="handleCancel">
            <p>{{ModalText}}</p>
        </a-modal>
    </div>
</template>
<script>
import AModal from 'ant-design-vue/lib/modal';
export default {
  components: {
    AModal
  },
  data() {
    return {
      maskClosable:false,
      ModalText: "自定义",
      visible: false,
      confirmLoading: false,
      searchCustomerInfo: false,
      importCustomerInfo: false
    };
  },
  mounted() {},
  methods: {
    showModal(key) {
      this.ModalText = "表格中第" + (key+1) + "条录音";
      this.visible = true;
    },
    handleOk(e) {
      this.ModalText = "The modal will be closed after two seconds";
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
      }, 2000);
    },
    handleCancel(e) {
      console.log("Clicked cancel button");
      this.visible = false;
    }
  }
};
</script>



