<template>
  <page-layout :title="title">
    <template slot="action">
      <div class="interval-sovle">
        <a-button-group style="margin-right: 8px">
          <a-button
            icon="rollback"
            @click="backCustomerInfo"
          >返回</a-button>
          <a-button
            icon="delete"
            :loading="loadingDeleteBtn"
            :class="isCloseEvents?'close_events':''"
            @click="showDeleteConfirm(currentCustInfo.id)"
          >删除</a-button>
          <a-button @click="showTrackRecordModal">
            跟踪记录
            <trackRecordModal :ID="currentCustInfo.id" ref="dialogtreeTrackRecord"/>
          </a-button>
          <a-button>上年保险记录</a-button>
        </a-button-group>
        <a-button-group>
          <a-button
            icon="left"
            type="primary"
            :class="isCloseEvents?'close_events':''"
            :loading="loadingPreviousBtn"
            @click="previous"
          >
            上一条
          </a-button>
          <a-button
            type="primary"
            :class="isCloseEvents?'close_events':''"
            :loading="loadingNextBtn"
            @click="next"
          >
            下一条
            <a-icon type="right" />
          </a-button>
        </a-button-group>
      </div>
    </template>
    <div class="detailCustomerInfo">
      <a-tabs v-model="activeKey">
        <a-tab-pane
          tab="详细信息"
          key="1"
        >
          <a-spin
            :spinning="spinning"
            size="default"
          >
            <customer-info-handle
              ref="dialogtreeCustInfoHan"
            />
          </a-spin>
        </a-tab-pane>
        <a-tab-pane
          tab="跟踪记录"
          key="2"
        >
          <a-spin
            :spinning="spinning"
            size="default"
          >
            <trackRecord/>
          </a-spin>
        </a-tab-pane>
        <a-tab-pane
          tab="算价"
          key="3"
        >
          <a-spin
            :spinning="spinning"
            size="default"
          >
            <calculatePrice ref="dialogtreeCalPirce"/>
          </a-spin>
        </a-tab-pane>
          <!-- :disabled="!isDisabled" -->
        <!-- <a-button
          v-if="activeKey=='1'&&device != 'mobile'"
          type="primary"
          :class="isCloseEvents?'close_events':''"
          :loading="submitLoading"
          slot="tabBarExtraContent"
          @click="submitInfo"
        >保存</a-button>
        <a-button
          v-if="activeKey=='1'&&device != 'mobile'"
          slot="tabBarExtraContent"
          :class="isCloseEvents?'close_events':''"
          :loading="resetLoading"
          @click="resetInfo"
        >重置修改项</a-button> -->
        <!-- <a-button
          v-if="activeKey=='3'&&device != 'mobile'"
          slot="tabBarExtraContent"
          type="primary"
          :class="isCloseEvents?'close_events':''"
          :loading="resetLoading"
        >保存</a-button>
        <a-button-group v-if="activeKey=='3'&&device != 'mobile'" slot="tabBarExtraContent">
          <a-button
            :class="isCloseEvents?'close_events':''"
            :loading="resetLoading"
          >另存为</a-button>
          <a-button
            :class="isCloseEvents?'close_events':''"
            :loading="resetLoading"
          >发送短信</a-button>
          <a-button
            :class="isCloseEvents?'close_events':''"
            :loading="resetLoading"
          >提交核保</a-button>
        </a-button-group> -->
      </a-tabs>
    </div>
  </page-layout>
</template>
<script>
import Vue from "vue";
import ATabs from "ant-design-vue/lib/tabs";
import ASpin from "ant-design-vue/lib/spin";
import AButton from "ant-design-vue/lib/button";
import AModal from "ant-design-vue/lib/modal";
import AIcon from 'ant-design-vue/lib/icon'
import CustomerInfoHandle from "./CustomerInfoHandle.vue";
import calculatePrice from "./CalculatePrice.vue";
import trackRecordModal from "./TrackRecordModal.vue";
import trackRecord from "./TrackRecord.vue";
import { mapGetters, mapMutations } from "vuex";
import PageLayout from "@/components/Page/PageLayout";
import { mixinDevice } from '@/utils/mixin.js'
const ATabPane = ATabs.TabPane;
const AButtonGroup = AButton.Group;
Vue.prototype.$confirm = AModal.confirm;

export default {
  components: {
    ATabs,
    ASpin,
    AButton,
    AButtonGroup,
    ATabPane,
    AIcon,
    CustomerInfoHandle,
    calculatePrice,
    trackRecordModal,
    trackRecord,
    PageLayout
  },
  mixins:[mixinDevice],
  data() {
    return {
      currentIndex: -1,
      spinning: false,
      activeKey: "1",
      loadingNextBtn: false,
      loadingPreviousBtn: false,
      loadingDeleteBtn: false,
      resetLoading: false
    };
  },
  computed: {
    ...mapGetters(["currentCustInfo", "condition"]),
    title() {
      return this.$route.meta.title;
    },
    isCloseEvents() {
      return (
        this.loadingNextBtn ||
        this.loadingPreviousBtn ||
        this.loadingDeleteBtn
      );
    },
    // isDisabled(){
    //   return !this.$refs.dialogtreeCustInfoHan?false:this.$refs.dialogtreeCustInfoHan.custInfoDataNotNull()
    // }
  },
  watch: {
    $route(to, from) {
      if (to.path == "/customer/detail") {
        if (this.activeKey) {
          this.activeKey = "1";
        }
      }
    }
  },
  methods: {
    // submitInfo(e) {
    //   let _this = this
    //   this.submitLoading = true;
    //   this.$refs.dialogtreeCustInfoHan.submitToChild().then((result) => {
    //     _this.submitLoading = false
    //   }).catch((err) => {
    //     _this.submitLoading = false
    //   });
    // },
    // resetInfo() {
    //   let _this = this
    //   this.resetLoading = true;
    //   this.$refs.dialogtreeCustInfoHan.resetToChild().then((result) => {
    //     _this.resetLoading = false
    //   }).catch((err) => {
    //     _this.resetLoading = false
    //   });
    // },
    resetactiveKey() {
      this.activeKey = "1";
    },
    //      从url获取参数id
    parseUrl() {
      var searchHref = window.location.search.replace("?", "");
      var params = searchHref.split("&");
      var returnParam = {};
      params.forEach(function(param) {
        var paramSplit = param.split("=");
        returnParam[paramSplit[0]] = paramSplit[1];
      });
      return returnParam;
    },
    backCustomerInfo() {
      window.history.go(-1);
    },
    //      显示跟踪记录模态框
    showTrackRecordModal() {
      this.$refs.dialogtreeTrackRecord.showModal();
    },
    //      删除确认对话框
    showDeleteConfirm(id) {
      let _this = this;
      this.$confirm({
        title: "你确定要删除该客户信息吗?",
        content: "Some descriptions",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          _this.spinning = true;
          // ajax request after empty completing
          _this.sendDeleteDataToServer(id);
        },
        onCancel() {
        }
      });
    },
    ...mapMutations({
      setCurrentCustInfo: "CURRENT_CUST_INFO"
    }),
    //       getCurrentIndex(){
    //         let  _this = this
    //         for (let [index, elem] of this.customersInfo.entries()) {
    //           if (elem.id === Number(_this.currentCustInfo.id)) {
    // //            console.log("上一条id:" + _this.customersInfo[index - 1].id)
    //             return index
    //           }
    //         }
    //       },
    getPreAndNextFromServer(method) {
      const params = this.condition;
      params.operation = method;
      let _this = this;
      return new Promise(function(resolve, reject) {
        _this.$api
          .getPreAndNextCustInfo(_this.currentCustInfo.id, params)
          .then(result => {
            _this.setCurrentCustInfo({ id: result.data.id });
            // this.customersInfo = result.data.items
            resolve(result);
          })
          .catch(err => {
            _this.loadingNextBtn = false;
            _this.loadingPreviousBtn = false;
            _this.$myNotification.sendFailedNotification(err)
            reject(err);
          });
      });
    },
    previous() {
      // this.$message.warning("此处上一条操作，改为通过请求直接获得数据");
      let _this = this;
      this.loadingPreviousBtn = true;
      this.resetactiveKey()
      this.getPreAndNextFromServer("previous")
        .then(result => {
          _this.$refs.dialogtreeCustInfoHan
            .getCustomerDetailInfoFromServer()
            .then(result => {
              (_this.$refs.dialogtreeCalPirce||{}).updateChild()
              _this.loadingPreviousBtn = false;
            })
            .catch(err => {
              _this.loadingPreviousBtn = false;
            });
        })
        .catch(err => {
          _this.loadingPreviousBtn = false;
        });
      // let currentIndex = this.getCurrentIndex()
      // if (0 === currentIndex) {
      //   this.$message.warning('这已经是第一条了', 3);
      // } else if (0 > currentIndex) {
      //   this.$message.error('索引错误，将显示第一条', 5);
      // } else {
      //   const obj={
      //     id:this.customersInfo[currentIndex - 1].id,
      //     owner:this.customersInfo[currentIndex - 1].owner,
      //     mobile:this.customersInfo[currentIndex - 1].mobile
      //   }
      //   this.setCurrentCustInfo(obj)
      // }
      // return this.currentIndex = this.currentCustInfo.id
    },
    next() {
      let _this = this;
      this.loadingNextBtn = true;
      this.resetactiveKey()
      this.getPreAndNextFromServer("next")
        .then(result => {
          _this.$refs.dialogtreeCustInfoHan
            .getCustomerDetailInfoFromServer()
            .then(result => {
              (_this.$refs.dialogtreeCalPirce||{}).updateChild()
              _this.loadingNextBtn = false;
            })
            .catch(err => {
              _this.loadingNextBtn = false;
            });
        })
        .catch(err => {
          _this.loadingNextBtn = false;
        });
    },
    sendDeleteDataToServer(id) {
      let _this = this;
      this.loadingDeleteBtn = true;
      this.$api
        .deleteCustomerInfo(id)
        .then(result => {
          const params = {
            where: _this.condition.where,
            order: _this.condition.order,
            orderby: _this.condition.orderby,
            page: _this.condition.page
          };
          _this.spinning = false;
          _this.$router.push("/customer/list");
          _this.loadingDeleteBtn = false;
        })
        .catch(err => {
          _this.spinning = false;
          _this.loadingDeleteBtn = false;
          _this.$myNotification.sendFailedNotification(err)
        });
    }
  }
};
</script>
<style>
</style>




