<template>
  <div class="calculatePrice">
    <a-spin :spinning="spinning" size="default">
      <calculate-price-history-three />
      <calculate-info ref="dialogtreeCalInfo" :id="currentCustInfo.id">
        <template slot="calculateBtn">
          <a-button type="primary" style="margin-right: 8px" icon="calculator" @click="calculatePrice()">
            算价
          </a-button>
          <a-button type="primary" icon="thunderbolt" @click="calculatePrice()">
            快速报价
          </a-button>
        </template>
      </calculate-info>
      <calculatePriceTableBottom style="flex:1" />
    </a-spin>
    <footer-tool-bar :style="{ width: isSideMenu() && isDesktop() ? `calc(100% - ${sidebarOpened ? 256 : 80}px)` : '100%'}">
      <div class="interval-sovle">
        <a-button style="margin-right: 8px">
          发送短信
        </a-button>
        <a-button style="margin-right: 8px">
          提交核保
        </a-button>
        <a-button type='primary'>
                保存
        </a-button>
      </div>
    </footer-tool-bar>
  </div>
</template>

<script>
  import ASpin from "ant-design-vue/lib/spin";
  import AButton from "ant-design-vue/lib/button";
  import calculatePriceTableBottom from "./CalculatePriceTableBottom.vue";
  import CalculatePriceHistory from './CalculatePriceHistory'
  import CalculatePriceHistoryThree from './CalculatePriceHistoryThree'
  import CalculateInfo from './CalculateInfo.vue'
  import FooterToolBar from '@/components/FooterToolbar'
  import {
    mapGetters,
    mapMutations
  } from "vuex";
  import {
    mixin,
    mixinDevice
  } from "@/utils/mixin.js";
  
  import moment from "moment";


  // In the fifth row, other columns are merged into first column
  // by setting it's colSpan to be 0



  const renderContent = (value, row, index) => {
    const obj = {
      children: value,
      attrs: {}
    };
    return obj;
  };
  const paramMap = {
    "MODEL": "model",
    "VIN": "vin",
    "MODEL_CODE": "modelCode"
  }
  export default {
    components: {
      ASpin,
      AButton,
      calculatePriceTableBottom,
      CalculatePriceHistory,
      CalculateInfo,
      FooterToolBar,
      CalculatePriceHistoryThree
    },
    mixins: [mixin,mixinDevice],
    data() {
      return {
        spinning: false,
        data: [],
        custInfoData: {},
        business: [],
      };
    },
    methods: {
      updateChild(){
        (this.$refs.dialogtreeCalInfo||{}).updateCalPrice()
      },
      getCaculateRecordsFromServer() {
        let _this = this
        return new Promise((resolve, reject) => {
          _this.$api.getCaculateRecords().then((result) => {
            resolve(result)
          }).catch((err) => {
            reject(err)
          });
        })
      },
      spinningCalculatePrice() {
        this.spinning = true;
        setTimeout(() => {
          this.spinning = false;
        }, 2000);
      },
      
      calculatePrice() {
        this.spinningCalculatePrice();
        let _this = this;
      //   alert(
      //         "信息：" +
      //         JSON.stringify(values) +
      //         " \n " +
      //         "----------------------------------------------" +
      //         " \n " +
      //         "投保情况："
      //         // _this.getSelectedInsuredPrj()  改为调用子组件...buttom中的该方法            );
      //   )
      },
      
    }
  };
</script>

<style>
  .calculatePrice table.cal-pri-info {
    width: 100%
  }
  
  @media(max-width:1550px) {
    .calculatePrice table.cal-pri-info {
      width: 1200px
    }
  }
</style>




