import { axios } from '@/utils/request'

const api = {
    trackRecord: '/accounttrack',
}
export default api

/**
 * login func
 * parameter: {
 *     username: '',
 *     password: '',
 *     remember_me: true,
 * }
 * @param parameter
 * @returns {*}
 */
export function addTrackRecord(parameter) {
    return axios({
        url: api.trackRecord,
        method: 'post',
        data: parameter
    })
}


