<template>
    <a-modal title="查车型" v-model="visible" @ok="handleOk" width="1148px">
        <a-table :rowSelection="{selectedRowKeys: selectedRowKeys,onChange:onSelectChange,type:'radio'}" :columns="columns" :dataSource="data" :scroll="{ x: 1100 }" :loading="loading" bordered >
        </a-table>
    </a-modal>
</template>

<script>
    import AModal from "ant-design-vue/lib/modal";
    import ATable from "ant-design-vue/lib/table";
    import {
        queryVehicleType
    } from '@/api/calculate.js'

    export default {
        props: {
            params: {
                type: Object,
                default: {}
            }
        },
        components: {
            AModal,
            ATable
        },
        data() {
            return {
                visible: false,
                data: [],
                loading:false,
                selectedRowKeys:[]
            };
        },
        computed: {
            columns() {
                const columns = [{
                        title: "型号代码",
                        dataIndex: "id",
                        key: "id",
                    },
                    {
                        title: "品牌型号",
                        dataIndex: "name",
                        key: "name",
                    },
                    {
                        title: "车型别名",
                        dataIndex: "alias",
                        key: "alias",
                    },
                    {
                        title: "购置价",
                        dataIndex: "purchasePrice",
                        key: "purchasePrice",
                    },
                    {
                        title: "上市年份",
                        dataIndex: "year",
                        key: "year",
                    },
                    {
                        title: "核定载客",
                        dataIndex: "seats",
                        key: "seats",
                    },
                    {
                        title: "整备质量",
                        dataIndex: "weight",
                        key: "weight",
                    },
                    {
                        title: "车辆种类",
                        dataIndex: "class",
                        key: "class",
                    },
                    {
                        title: "排量",
                        dataIndex: "displacement",
                        key: "displacement",
                    },
                    {
                        title: "车辆品牌",
                        dataIndex: "brand",
                        key: "brand",
                    },
                    {
                        title: "品牌对应型号",
                        dataIndex: "brandType",
                        key: "brandType",
                    },
                    {
                        title: "车型功率",
                        dataIndex: "power",
                        key: "power",
                    },
                    {
                        title: "吨位",
                        dataIndex: "tonnage",
                        key: "tonnage",
                    },
                    {
                        title: "产地",
                        dataIndex: "production",
                        key: "production",
                    },
                ]
                return columns
            }
        },
        methods: {
            onSelectChange(selectedRowKeys) {
                this.selectedRowKeys = selectedRowKeys;
            },
            queryVehicleTypeFromServer(data) {
                let _this = this;
                return new Promise((resovle, reject) => {
                    queryVehicleType(data)
                        .then(result => {
                            resovle(result);
                        })
                        .catch(err => {
                            _this.loading = false
                            _this.$myNotification.sendFailedNotification(err);
                            reject(err);
                        });
                });
            },
            showModal() {
                let _this = this
                this.visible = true;
                this.loading = true
                this.queryVehicleTypeFromServer(this.$props.params).then((result) => {
                    result.data.items.forEach((item, index) => {
                        item.key = index
                    })
                    _this.data = result.data.items
                    _this.loading = false
                })
            },
            handleOk(e) {
                const model = this.data.filter((item,index)=>index==this.selectedRowKeys[0])[0]
                this.visible = false;
                this.$emit('getModel',model)
            },
        }
    };
</script>

<style>
    
</style>



