<template>
  <a-card :loading="loading" class="list-card" :body-style="{ padding: '10px 20px 0' }" >
    <div class="list-card-header">
      <div class="meta">
        <span class="list-card-title">{{ title||'空标题...' }}</span>
        <span class="list-card-action">
          <slot name="action"></slot>
        </span>
      </div>
      <div class="description"><span>{{ description||'没有填写描述信息...' }}</span></div>
    </div>
    <div class="list-card-content">
      <div class="list-card-time"><span>{{ time||'1970-01-01 00:00:00' }}</span></div>
    </div>
  </a-card>
</template>

<script>
  import ACard from 'ant-design-vue/lib/card'
  export default {
    name: 'ListCard',
    props: {   
      title: {
        type: String,
        default: ''
      },
      description: {
        type: String,
        default: ''
      },
      time:{
        type: String,
        default: ''
      },
      loading: {
        type: Boolean,
        default: false
      }
    },
    components:{
      ACard
    }
  }
</script>

<style lang="scss" scoped>
  .list-card{
    border-radius: 10px;
    border-width:2px;
    border-left-width: 10px;
    border-color: #d9d9d9;
    border-left-color: #ffa940;
    &:hover{
      background: #fff7e6;
    }
  }
  
  .list-card-header {
    position: relative;
    overflow: hidden;
    width: 100%;
    margin-bottom: 5px;

    .meta {
      position: relative;
      overflow: hidden;
      margin-bottom: 5px;
      color: rgba(0,0,0,.85);
        line-height: 32px;
        height: 33px;
        vertical-align: top;
        font-size: 14px;
    }
  }

  .list-card-action {
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
  }

  .list-card-footer {
    border-top: 1px solid #e8e8e8;
    padding-top: 9px;
    margin-top: 8px;

    > * {
      position: relative;
    }

    .field {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      margin: 0;
    }
  }

  .list-card-content {
    margin-bottom: 12px;
    width: 100%;
  }

  .description {
    padding-right:33px;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    white-space: nowrap;
    color: rgba(0,0,0,.45);
    line-height: 22px;
  }
  .list-card-time{
    white-space: nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
  }
</style>


